:root {
  --mainOpacity: rgba(0, 0, 0, 0.55);
}

.dark-overlay {
  background-color: rgba(0, 0, 0, 0.35);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.main-color {
  background-color: #975a5e;
}

.inactiveLink {
  pointer-events: none;
  cursor: default;
}

.images-container {
  max-width: 1200px;
  margin: 2rem auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(15rem, 1fr));
  gap: 1rem;
}

.image {
  width: 100%;
  height: 20rem;
  cursor: pointer;
}

.image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 8px;
}

.input-lg {
  width: 100%;
  height: 50px;
  padding: 10px;
  font-size: 16px;
}
.social {
  list-style: none;
  padding: 0;
  display: flex;
  justify-content: center;
}

.social li {
  margin: 0 10px;
}

.social a {
  text-decoration: none;
  color: inherit;
}

.social .icon {
  font-size: 1.5rem;
}

.social .list-inline-item .icon:hover {
  color: #975a5e;
  transform: scale(1.1);
  transition: color 0.3s ease, transform 0.3s ease;
}

.text-brown {
  color: #975a5e;
}

.card img {
  border-radius: 5px;
}

.card-title {
  font-weight: bold;
}

.card-text {
  font-size: 0.9rem;
}

.footer-container a {
  color: #ffffff;
  transition: color 0.3s;
}

.footer-container a:hover {
  color: #f3cba5;
}

.footer-container h5 {
  color: #f3cba5;
}

.card-hover {
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.card-hover:hover {
  transform: scale(1.05);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}
